import React from 'react'
import { graphql } from 'gatsby'
import { Helmet } from 'react-helmet'
import { Jumbotron } from 'reactstrap'
import MDXRenderer from 'gatsby-plugin-mdx/mdx-renderer'

import BreadcrumbNav from '../components/breadcrumb-nav'
import Layout from '../components/layout'
import Toc from '../components/toc'
import {
  flattenFileNode,
  matterNameFromMatter,
  classeNameFromClasse,
  classeWikidataURIFromClasse,
} from '../helpers'

// Pour l'instant on utilise mathjax comme sur drupal
// import 'katex/dist/katex.min.css'
const MathJaxConfig = `
MathJax = {
  tex: {
    inlineMath: [['$', '$'], ['\\(', '\\)']]
  },
  svg: {
    fontCache: 'global'
  }
};
`

export default ({ data, location }) => {
  const exo = flattenFileNode(data.mdx)

  const title = `${exo.classe} | ${exo.matter} | ${exo.part} | ${exo.chapter}`

  const description = `Cours de ${classeNameFromClasse(
    exo.classe
  )} en ${matterNameFromMatter(exo.matter)} intitulé: «${exo.chapter}»`

  return (
    <Layout location={location} data={data}>
      <Helmet title={`${title}`} htmlAttributes={{ class: `exo` }}>
        <script type="application/ld+json">{`
         {"@context": "http://schema.org/",
          "@type": "exercice",
          "name": "${exo.part} | ${exo.chapter}",
          "description": "${description}",
          "learningResourceType": [
            "lesson"    ],
          "provider": {
            "@type": "schema:EducationalOrganization",
            "name": "lyceum.fr"
          },
          "audience": {
            "@type": "EducationalAudience",
            "éducationRole": "student"
          },
          "educationalAlignment": {
            "@type": "AlignmentObject",
            "alignmentType": "educationalLevel",
            "educationalFramework": "French Grade Levels",
            "targetName": "${classeNameFromClasse(exo.classe)}",
            "@id": "${classeWikidataURIFromClasse(exo.classe)}"
          },
          "url": "${location.href}"
        }
    `}</script>
        <script>{`
MathJax = {
  tex: {
    inlineMath: [['$', '$']]
  },
  svg: {
    fontCache: 'global'
  }
};
`}</script>
        <script
          type="text/javascript"
          id="MathJax-script"
          async
          src="https://cdn.jsdelivr.net/npm/mathjax@3/es5/tex-svg.js"
        ></script>
      </Helmet>
      <BreadcrumbNav node={exo} />
      <Jumbotron>
        <h1 className="display-3">Exercices</h1>
        <strong className="display-4">{`Chapitre ${exo.nChapter}: ${exo.chapter}`}</strong>
        <div id="toc" className="large-4 columns"></div>
      </Jumbotron>
      <MDXRenderer className="exo">{exo.body}</MDXRenderer>
    </Layout>
  )
}

export const query = graphql`
  query MDXExoQuery($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      body
      fields {
        classe
        chapter
        matter
        nChapter
        part
        nPart
        type
      }
    }
  }
`
